import { isLocalHubsUrl, isLocalHubsSceneUrl, isHubsRoomUrl, isLocalHubsAvatarUrl } from "../utils/media-url-utils";
import { guessContentType } from "../utils/media-url-utils";
import { handleExitTo2DInterstitial } from "../utils/vr-interstitial";
import { changeHub } from "../change-hub";
import { exists } from "fs-extra";

AFRAME.registerComponent("open-media-button", {
  schema: {
    onlyOpenLink: { type: "boolean" }
  },
  init() {
    this.label = this.el.querySelector("[text]");

    this.updateSrc = async () => {
      if (!this.targetEl.parentNode) return; // If removed
      const mediaLoader = this.targetEl.components["media-loader"].data;
      const src = (this.src = (mediaLoader.mediaOptions && mediaLoader.mediaOptions.href) || mediaLoader.src);
      const visible = src && guessContentType(src) !== "video/vnd.hubs-webrtc";
      const mayChangeScene = this.el.sceneEl.systems.permissions.canOrWillIfCreator("update_hub");

      this.el.object3D.visible = !!visible;

      if (visible) {
        let label = "open link";
        if (!this.data.onlyOpenLink) {
          let hubId;
          if (await isLocalHubsAvatarUrl(src)) {
            label = "use avatar";
          } else if ((await isLocalHubsSceneUrl(src)) && mayChangeScene) {
            label = "use scene";
          } else if ((hubId = await isHubsRoomUrl(src))) {
            const url = new URL(src);
            if (url.hash && window.APP.hub.hub_id === hubId) {
              label = "go to";
            } else {
              label = "visit room";
            }
          }
        }
        this.label.setAttribute("text", "value", label);
      }
    };

    this.onClick = async () => {
      const mayChangeScene = this.el.sceneEl.systems.permissions.canOrWillIfCreator("update_hub");

      const exitImmersive = async () => await handleExitTo2DInterstitial(false, () => {}, true);

    let hubId;
      if (this.data.onlyOpenLink && shouldShowVideoAtCurrentTime()) {
        await exitImmersive();
        window.open(this.src);
      }
      else if (await isLocalHubsAvatarUrl(this.src)) {
        const avatarId = new URL(this.src).pathname.split("/").pop();
        window.APP.store.update({ profile: { avatarId } });
        this.el.sceneEl.emit("avatar_updated");
      } else if ((await isLocalHubsSceneUrl(this.src)) && mayChangeScene) {
        this.el.sceneEl.emit("scene_media_selected", this.src);
      } else if ((hubId = await isHubsRoomUrl(this.src))) {
        const url = new URL(this.src);
        if (url.hash && window.APP.hub.hub_id === hubId) {
          // move to waypoint w/o writing to history
          window.history.replaceState(null, null, window.location.href.split("#")[0] + url.hash);
        } else if (APP.store.state.preferences.fastRoomSwitching && isLocalHubsUrl(this.src)) {
          // move to new room without page load or entry flow
          changeHub(hubId);
        } else {
          await exitImmersive();
          location.href = this.src;
        }
      } 
      //   else {
      //   await exitImmersive();
	    //   const srcSplit = this.src.split("_");
      // 	if (srcSplit[0] === "btn") {
	    //     fnClickObj(this.src); 
      // 	} else {
      // 	  window.open(this.src);
	    //   }
      // } 
    };

    /*비디오 시간 설정 */
    function shouldShowVideoAtCurrentTime() {
      const now = new Date();
      const hours = now.getHours();

      return [24, 2, 4, 6].includes(hours);
    }
    /*시간 설정 끝*/


    //fnClickObj
    // function fnClickObj(src) {
    //   console.log("fnClickObj 시작");
      
    //   if( (screen.width <= 640) || (window.matchMedia && window.matchMedia('only screen and (max-width: 640px)').matches)) {
    //     //Mobile
    //     document.getElementById("custom-popup-mobile").style.display = "none";

    //     var str = "<table style='width:300px;' cellpadding='0' cellspacing='0' border='0'>";
    //     str += "<tr><td><div align='center'><img src='https://metatest.nztoverse.com/img/guest_top_mobile.png' style='width:300px; height:50px;' id='btnClickClose-mobile'></div></td></tr>";
    //     str += "<tr><td style='text-align:center; padding-top:0px; padding-bottom:0px;' align='center'><div align='center'><iframe src='https://metatest.nztoverse.com/_quiz.html' style='width:300px; height:400px;' frameborder=0 framespacing=0 marginheight=0 marginwidth=0 scrolling=yes vspace=0></iframe></div></td></tr>";
    //     str += "</table>";
		
    //     document.getElementById("custom-popup-text-mobile").innerHTML = str;
    //     document.getElementById("custom-popup-mobile").style.display = "flex";
    //     document.getElementById('btnClickClose-mobile').addEventListener("click", function() { document.getElementById("custom-popup-mobile").style.display = "none"; });
    //   } else {
    //     document.getElementById("custom-popup").style.display = "none";

    //     var str = "<table style='width:800px;' cellpadding='0' cellspacing='0' border='0'>";
    //     str += "<tr><td><div align='center'><img src='https://metatest.nztoverse.com/img/guest_top_pc.png' style='width:800px; height:50px;' id='btnClickClose'></div></td></tr>";
    //     str += "<tr><td style='text-align:center; padding-top:0px; padding-bottom:0px;' align='center'><div align='center'><iframe src='https://metatest.nztoverse.com/_quiz.html' style='width:800px; height:600px;' frameborder=0 framespacing=0 marginheight=0 marginwidth=0 scrolling=yes vspace=0></iframe></div></td></tr>";
    //     str += "</table>";

    //     document.getElementById("custom-popup-text").innerHTML = str;
    //     document.getElementById("custom-popup").style.display = "flex";
    //     document.getElementById('btnClickClose').addEventListener("click", function() { document.getElementById("custom-popup").style.display = "none"; });
	  //   }
    // }
    //

    NAF.utils.getNetworkedEntity(this.el).then(networkedEl => {
      this.targetEl = networkedEl;
      this.targetEl.addEventListener("media_resolved", this.updateSrc, { once: true });
      this.updateSrc();
    });
  },
  play() {
    this.el.object3D.addEventListener("interact", this.onClick);
  },

  pause() {
    this.el.object3D.removeEventListener("interact", this.onClick);
  }
});
